import React from 'react';

import './RegistrationForm.css';

export default class RegistrationForm extends React.Component {


    constructor(){
        super()
        this.state = {
            name : null,
            surname : null,
            phone : null,
            password : null,
            data : null,
            login : true,
            array:[ ],
        }
    }

    onChangeName = (event) => {
            this.setState({
                name : event.target.value
           })
    }

    onChangeSurname = (event) => {
        this.setState({
            surname : event.target.value
       })
    }

    onChangePhone = (event) => {
        const value = event.target.value
        // const valueStr = JSON.stringify(value)

        // const num = localStorage;
        // console.log( num);
        
        // for(let key in num){
        //     console.log(key)
            
        // }
            
        
        
        // if(localStorage.valueStr === undefined ){
        //     console.log(false)
        // } else {
        //     console.log(true)
        // }
        this.setState({
            phone : value
       })
    }

    onChangePassword = (event) => {
        
        this.setState({
            password : event.target.value
       })
    }

    onChangeData = (event) => {
        this.setState({
            data : event.target.value
       })
    }

    onSubmitFn() {
        const localStorId = this.state.phone;
        localStorage.setItem(`${localStorId}`, JSON.stringify(this.state));
        
        this.props.login(this.state);
        this.props.reset();
        // if(localStorage.localStorId !== undefined){
            
        // }else {
        //     console.log('—Ç–∞–∫–æ–π user —É–∂–µ –µ—Å—Ç—å')
        // }  
        // localStorage.removeItem(localStorId);
        
        
    }

    render(){

        return(
            <div  className="registration-form">
                <div><input onChange={this.onChangeName} placeholder="–ò–º—è"/></div>
                <div><input onChange={this.onChangeSurname} placeholder="–§–∞–º–∏–ª–∏—è"/></div>
                <div><input onChange={this.onChangePhone} placeholder="–¢–µ–ª–µ—Ñ–æ–Ω"/></div>
                <div><input onChange={this.onChangePassword} placeholder="–ø–∞—Ä–æ–ª—å"/></div>
                <div>
                    <input onChange={this.onChangeData} type="date" />
                </div>
                <button onClick={this.onSubmitFn.bind(this)}>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
            </ div>
        )
    }
}